class Form extends Component {

    // myData = []

    constructor(props) {
        super(props);
        this.state = {fulllist: "", listNums:[], myData:[]};
        this.handleChange = this.handleChange.bind(this);
        // this.handleSubmit = this.handleSubmit.bind(this);
        
    }

    calcCrossOneZero(theWall, pos1, pos2) {

        var oneCrossNumerator = new bigDecimal(-theWall[pos1-4][pos2]*theWall[pos1-1][pos2]**2+theWall[pos1-2][pos2-2]*theWall[pos1-2][pos2+1]**2+theWall[pos1-2][pos2-1]**2*theWall[pos1-2][pos2+2]);
        var oneCrossDenominator = new bigDecimal(theWall[pos1-3][pos2]**2)
        var ans = oneCrossNumerator.divide(oneCrossDenominator);// ((-theWall[pos1-4][pos2]*theWall[pos1-1][pos2]**2+theWall[pos1-2][pos2-2]*theWall[pos1-2][pos2+1]**2+theWall[pos1-2][pos2-1]**2*theWall[pos1-2][pos2+2])/(theWall[pos1-3][pos2]**2))
        var ans2 = ans.round();
        console.log("Calculating Cross One Zero", ans.getValue())
        var ans3 = parseFloat(ans2.getValue());
        var ans4 = Math.round(ans3);
        return ans4;
    }

    calcCross(theWall, pos1, pos2) {
        console.log("Called calc cross")
        console.log(theWall)
        console.log(pos1, pos2)

        if (theWall[pos1-2][pos2]==0) {
            return this.calcCrossOneZero(theWall, pos1, pos2)
        }
        else {
            var crossNumerator = new bigDecimal(BigInt(BigInt(theWall[pos1-1][pos2])**2-BigInt(theWall[pos1-1][pos2-1])*BigInt(theWall[pos1-1][pos2+1])));
            var crossDenominator = new bigDecimal(BigInt((theWall[pos1-2][pos2])));
            console.log("CrossNumer", crossNumerator.getValue())
            console.log("CrossDenom", crossDenominator.getValue())
            var ans = (crossNumerator.divide(crossDenominator));
            console.log("Calculating Cross No Zeros", ans.getValue())
            var ans2 = ans.round();
            var ans3 = parseFloat(ans2.getValue());
            var ans4 = Math.round(ans3);

            return ans4;
        }
    }

    determineZeroSquareDimensions(theWall, myRow, eye) {
        var ct1 = 0
        var ct2 = 0
        while(theWall[myRow-1-ct1][eye]==0) {
            ct1 += 1
        }
        var ct3 = 0
        
        while(theWall[myRow-ct1][eye+ct3]==0) {
            ct3 += 1
        }

        //returning dimension and firstRow
        return [(ct3), (myRow-ct1)]
    }

    calcBrokenCross(theWall, theRow, theI, theDimension, theK, theMult) {
        var tM = [theWall[theRow-theDimension-2][theI], theWall[theRow-theDimension-2][theI-1]]
        var lM = [theWall[theRow-theDimension-1][theI-1], theWall[theRow-theDimension-2][theI-1]]
        var rM = [theWall[theRow-theDimension-1][theI+theDimension], theWall[theRow-theDimension][theI+theDimension]]
        var bM = [theMult*(rM[0]*lM[0]*tM[1]), (rM[1]*lM[1]*tM[0])]


        var blueNumerator  = new bigDecimal((lM[0]*theWall[theRow-theDimension-3][theI + theDimension -1 - theK]));
        var blueDenominator = new bigDecimal(theWall[theRow-theDimension-2][theI + theDimension - 1 - theK]*lM[1]);
        var blueItem = (blueNumerator.divide(blueDenominator));
        var greyNumerator = new bigDecimal((tM[0]*theWall[theRow-2-theK][theI - 2]));
        var greyDenominator = new bigDecimal((tM[1]*theWall[theRow-2-theK][theI - 1]));
        var greyItem = (greyNumerator.divide(greyDenominator));
        var yellowNums = [new bigDecimal(rM[0]), new bigDecimal(rM[1]*theWall[theRow-1][theI+theK])]
        var redNumerator = new bigDecimal(bM[0]*theWall[theRow-theDimension-1+theK][theI+theDimension+1])
        var redDenominator = new bigDecimal(bM[1]*theWall[theRow-theDimension-1+theK][theI+theDimension])
        var redItem = (redNumerator.divide(redDenominator))

        var yellowItem = yellowNums[1].divide(yellowNums[0])


        var ansp2 = new bigDecimal(((-1)**(theK)))
        var ansp22 = ansp2.multiply(greyItem)
        var ansp3 = new bigDecimal(((-1)**(theK+1)))
        var ansp33 = ansp3.multiply(redItem)
        var ansp4 = (blueItem.add(ansp22));
        var ansp5 = ansp4.add(ansp33);
        var ansp6 = ansp5.multiply(yellowItem);
        var ans2 = ansp6.round();
        var ans3 = parseFloat(ans2.getValue());
        var ans4 = Math.round(ans3);
        return ans4;
    }

    calculateInitials(theLineEntered) {
        var myBlock = []
        var myAns = []

        var limit = Math.floor((theLineEntered.length+1)/2)

        for(let i=0; i<limit; i++) {
            myBlock.push([])
            
            for(let j=limit-i-1; j<limit*2-i; j++) {
                myBlock[i].push(theLineEntered[j])
            }
        }
        console.log("MY BLOCK")
        console.log(myBlock)

        /// we get a 2x2 block once we have 3 entries.  And a 3x3 block once we have 5 entries.  And a 4x4
        /// once we have 7 entries.

        ///
        var limit2 = Math.floor((theLineEntered.length+1)/2)

        for (let i=2; i<limit2+1; i++) {
            let section = myBlock.slice(0,i).map(k => k.slice(0, i))
            console.log("Section:")
            console.log(section)
            var determinant = det(section)
            console.log(determinant)
            myAns.push(determinant)
        }

        return myAns

    }


    calcMyData(inList) {

        
        if(this.state.listNums != []) {
            var initials = this.calculateInitials(this.state.listNums)
            console.log("INITIALS")
            console.log(initials)
        }
        else {
            return [[0]]
        }
        

        var tempData = []

        tempData.push([])
        tempData.push([])
        tempData.push([])
        for (let i=0; i<this.state.listNums.length+2; i++) {
            tempData[0].push(0)
        }
        for (let i=0; i<this.state.listNums.length+1; i++) {
            tempData[1].push(1)
        }
        for (let i=0; i<this.state.listNums.length; i++) {
            tempData[2].push(this.state.listNums[i])
        }


        var firstPass = false;

        var startingCol = 0
        var count = 0
        
        for (let row=3; row<3 + initials.length; row++) {

            startingCol += 1
            // var startingCol = Math.floor((tempData[row-1].length+1)/2)
            console.log("Starting Column")
            console.log(startingCol)
            var onZeroSquare = false;
            tempData.push([])

            if(row > 3) {
                var prevRow =  row-1;
                var condition = false;

                var initialValue = tempData[prevRow][startingCol]
                
                for(let i=2; i< tempData[prevRow].length; i++){ 
                    if(tempData[prevRow][i] != initialValue) {
                        condition = true;
                    }
                }
                if(!condition) {
                    tempData[row].push(-99999)
                    break;
               }
            }
            
            
            
            
            for(let i=0; i<startingCol; i++) {
                tempData[row].push(NaN)
            }
            // for(let i=0; i<1; i++) {
            //     tempData[row].push(initials[count])
            //     count += 1
            // }

            var theZScount = -1;

            

            for(let i=startingCol; i<tempData[row-1].length-1; i++) {
                var dimension = -1
                var firstRow = -1

                if((tempData[row-2][i]==0 && tempData[row-1][i] ==0 && !onZeroSquare) || (tempData[row-3][i]==0 && tempData[row-2][i] ==0 && !onZeroSquare)){
                    if((tempData[row-3][i]==0 && tempData[row-2][i] ==0)){
                        var answ = this.determineZeroSquareDimensions(tempData, row-1, i)
                        dimension = answ[0]
                        firstRow  = answ[1]
                    } 
                    else {
                        var answ = this.determineZeroSquareDimensions(tempData, row, i)
                        dimension = answ[0]
                        firstRow  = answ[1]
                    }
                    onZeroSquare = true
                    firstPass = true
                    theZScount = dimension
                }
           
                else if (!(tempData[row-2][i]==0 && tempData[row-1][i] ==0) && !(tempData[row-3][i]==0 && tempData[row-2][i] ==0)) {
                    tempData[row].push(this.calcCross(tempData, row, i))
                }
                if(onZeroSquare && firstPass) {
                        firstPass = false

                        if(row-dimension > firstRow) {
                            var theMult = -999
                            if(dimension%2==0){
                                theMult = 1
                            }
                            else {
                                theMult = -1
                            }

                            var tM = -999
                            var lM = -999
                            var rM = -999
                            var bM = -999

                            if(row-dimension-1 > 0) {
                                if(tempData[row-dimension-1][i]!=0) {
                                    tM = (tempData[row-dimension-1][i], tempData[row-dimension-1][i-1])
                                    lM = (tempData[row-dimension][i-1], tempData[row-dimension-1][i-1])
                                    rM = (tempData[row-dimension-1][i+dimension], tempData[row-dimension][i+dimension])
                                    bM = (theMult*(rM[0]*lM[0]*tM[1]), (rM[1]*lM[1]*tM[0]))


                                    for (let k=0;k<dimension; k++) {
                                        tempData[row].push(tempData[row][i-1+k]*bM[1]/bM[0])
                                    }
                                }
                            }
                            else {
                                for (let k=0; k<dimension; k++) {
                                    var newNum = this.calcBrokenCross(tempData, row, i, dimension, k)
                                    tempData[row].push(newNum)
                                }
                            }
                        }
                        else {
                            for (let j=0; j<dimension; j++){
                                tempData[row].push(0)    
                            }
                        }
            

                }
            }

        
        }
        return tempData;
    }

    handleChange(evt) {
        this.setState({fulllist: evt.target.value, listNums:evt.target.value.split(",").map(Number)});
        // var cmd = this.calcMyData(this.state.listNums);
        this.setState({myData:this.calcMyData(this.state.listNums)});
        // await new Promise(r => setTimeout(r, 200));
        console.log("MYDATA");
        console.log(this.state.myData);
    }


    // handleSubmit(evt) {
    //     console.log(this.state.fulllist.split(','))
    //     // this.setState({});
    //     this.setState({fulllist:evt.target.value, listNums:evt.target.value.split(",")})
    //     evt.preventDefault()
    // }

    render() {

        
        

        var Boxes = this.state.listNums.map(numm => (<ColorBox 
            background="orange"
            numValue={numm}
            name='red 500'
            key='red'
            moreUrl='/palette/material-ui-colors/red'
            showingFullPallete={true}
        />));
        
        // var reducedData = 

        var Boxes2 = this.state.myData.map(row => {
            return (<tr>{row.map(ele => {
                var color
                if(isNaN(ele)) {
                    color="green"
                }
                else if(Math.round(ele) %2 == 0) {
                    color="red"
                }
                else {
                    color="orange"
                }

                return (<td><ColorBox 
                    background={color}
                    numValue={(ele)}
                    name='red 500'
                    key='red'
                    moreUrl='/palette/material-ui-colors/red'
                    showingFullPallete={true}
                /></td>
            );
            })}</tr>)});


        return (
            <div>
                <h1>Enter your sequence of numbers:</h1>
                <form>
                    <label htmlFor='fulllist'>Full List</label>
                    <input type='text'
                        size="1000"
                        id='fulllist'
                        name='fulllist'
                        defaultValue={this.state.fulllist}
                        onChange={this.handleChange}
                        />
                </form>
                <table className="tall-row">
                    {Boxes2}
                </table>
            </div>

        );
    }

}


export default Form;